@import url('https://fonts.googleapis.com/css2?family=Plus+Jakarta+Sans:ital,wght@0,200;1,200&display=swap');

.navSkills {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 80%;
    margin-top: 3rem;
    margin-bottom: 5rem;
    position: relative;
}

.skillsLink {
    font-family: "IBM Plex Sans", sans-serif;
    color: white;
    font-size: medium;
    transition: transform 0.2s ease-in-out;
    padding: 10px 20px;
    cursor: pointer;
    border: thick double rgb(var(--color));
}

.skillsLink:hover {
    transform: scale(1.05);
}


.skillsLink.active {
    background-color: white;
    color: black;
    transform: scale(1.15);
}
@media (max-width: 768px) {  
    .navSkills {
        flex-direction: column;
        align-items: center;
        width: 100%;
        margin-top: 2rem;
        margin-bottom: 3rem;
    }

    .skillsLink {
        font-size: 14px;  
        padding: 8px 15px;  
        margin-bottom: 1rem; 
    }

    .skillsLink:hover {
        transform: scale(1.03); 
    }

    .skillsLink.active {
        background: rgb(100, 150, 200);
        color: white;
    }
}
